import tuiButton from "@/components/extend/button/button"
const thorui = require("@/components/utils/clipboard.thorui.js")
import tuiFab from "@/components/tui-fab/tui-fab"
export default {
	components: {
		tuiButton,
		tuiFab
	},
	data() {
		return {
			left: 0,
			right: 80,
			bottom: 100,
			bgColor: "#5677fc",
			btnList: [],
			list: [{
				bgColor: "#16C2C2",
				//图标/图片地址
				imgUrl: "/static/images/fab/fab_about.png",
				//图片高度 rpx
				imgHeight: 64,
				//图片宽度 rpx
				imgWidth: 64,
				//名称
				text: "关于",
				//字体大小
				fontSize: 34,
				//字体颜色
				color: "#fff"
			}, {
				bgColor: "#64B532",
				//图标/图片地址
				imgUrl: "/static/images/fab/fab_share.png",
				//图片高度 rpx
				imgHeight: 64,
				//图片宽度 rpx
				imgWidth: 64,
				//名称
				text: "分享",
				//字体大小
				fontSize: 34,
				//字体颜色
				color: "#fff"
			}, {
				bgColor: "#FFA000",
				//图标/图片地址
				imgUrl: "/static/images/fab/fab_reward.png",
				//图片高度 rpx
				imgHeight: 64,
				//图片宽度 rpx
				imgWidth: 64,
				//名称
				text: "赞赏",
				//字体大小
				fontSize: 34,
				//字体颜色
				color: "#fff"
			}],
			list2: [{
				bgColor: "#16C2C2",
				//名称
				text: "关于",
				//字体大小
				fontSize: 28,
				//字体颜色
				color: "#fff"
			}, {
				bgColor: "#64B532",
				//名称
				text: "分享",
				//字体大小
				fontSize: 28,
				//字体颜色
				color: "#fff"
			}, {
				bgColor: "#FFA000",
				//名称
				text: "赞赏",
				//字体大小
				fontSize: 28,
				//字体颜色
				color: "#fff"
			}]
		}
	},
	onLoad() {
		this.btnList = [...this.list]
	},
	methods: {
		change(type) {
			switch (type) {
				case 1:
					this.left = 80;
					this.right = 0;
					break;
				case 2:
					this.left = 0;
					this.right = 80;
					break;
				case 3:
					this.bgColor = "#FF0000";
					break;
				case 4:
					this.btnList = [];
					break;
				case 5:
					this.btnList = [...this.list];
					break;
				case 6:
					this.btnList = [...this.list2];
					break;
				default:
					break;
			}
			this.tui.toast("切换成功，点击查看效果")
		},
		onClick(e) {
			let index = e.index

			switch (index) {
				case -1:
					this.tui.toast("您点击了悬浮按钮")
					break;
				case 0:
					uni.navigateTo({
						url:"/pages/about/about"
					})
					break;
				case 1:

					this.clipboard("https://thorui.cn/")











					break;
				case 2:
					uni.previewImage({
						urls: ["https://thorui.cn/img/reward.jpg"]
					})
					break;
				default:
					break;
			}
		},
		clipboard: function(data) {
			thorui.getClipboardData(data, (res) => {
				if (res) {
					this.tui.toast("分享链接已复制")
				} else {
					this.tui.toast("分享链接复制失败")
				}
			})
		}
	}
}
